.skilllist {
  display:flex;
  justify-content: center;
  gap: 1em;
  margin-top: 1em;
}

.skilllist > div {
  flex-grow: 1;
}

.skilllist h2 {
  background-color: black;
  color: white;
  padding: 0.3em;
}

@media only screen and (max-width: 600px) {
  .skilllist {
    flex-direction: column;
  }

  .skilllist > div {
    width: 100%;
  }
}